import React from 'react'
import clsx from 'clsx'
import Highlight, { defaultProps } from 'prism-react-renderer'
import {
  LiveProvider,
  LiveEditor,
  LiveError,
  LivePreview,
  LiveProviderProps
} from 'react-live'
import { mdx } from '@mdx-js/react'

import { prismTheme } from './prismTheme'

type ComponentProps<T> = T extends React.Component<infer U> ? U : never

export interface CodeBlockProps
  extends Omit<ComponentProps<Highlight>, 'children'> {
  children?: string
  className?: string
  live?: boolean
  preview?: boolean
  scope?: LiveProviderProps['scope']
}

export const CodeBlock: React.FC<any> = ({
  children,
  className: cls,
  language: lang,
  live,
  preview,
  scope,
  ...rest
}) => {
  const language = lang || (cls || '').replace(/language-/, '')
  const code = children?.trim() || ''

  if (live) {
    return (
      <div className={clsx('ba b--light-gray', cls)} {...rest}>
        <LiveProvider
          code={code}
          language={language}
          scope={{
            ...scope,
            mdx
          }}
          theme={prismTheme}
        >
          <LivePreview className="pa3 pa4-ns" />
          <LiveError className="dark-red mv0 nt4 pa3" />
          {/* https://github.com/satya164/react-simple-code-editor#props */}
          <div className="bg-near-white pa2">
            <LiveEditor
              style={{
                fontFamily: 'Consolas, monaco, monospace',
                fontSize: '14px',
                lineHeight: 1.25,
                overflowX: 'scroll',
                padding: 0
              }}
            />
          </div>
        </LiveProvider>
      </div>
    )
  } else if (preview) {
    return (
      <div className={clsx('ba b--light-gray', cls)} {...rest}>
        <LiveProvider
          code={code}
          language={language}
          scope={{
            ...scope,
            mdx
          }}
          theme={prismTheme}
        >
          <LivePreview className="pa3 pa4-ns" />
          <LiveError className="dark-red mv0 nt4 pa3" />
        </LiveProvider>
      </div>
    )
  }

  return (
    <Highlight
      {...defaultProps}
      code={code}
      language={language}
      theme={prismTheme}
      {...rest}
    >
      {({ className, style, tokens, getLineProps, getTokenProps }) => (
        <pre
          className={clsx('code f6 lh-title overflow-scroll pa3', className, cls)}
          style={style}
        >
          {tokens.map((line, i) => (
            <div {...getLineProps({ line, key: i })}>
              {line.map((token, key) => (
                <span {...getTokenProps({ token, key })} />
              ))}
            </div>
          ))}
        </pre>
      )}
    </Highlight>
  )
}
try {
    // @ts-ignore
    CodeBlock.displayName = "CodeBlock";
    // @ts-ignore
    CodeBlock.__docgenInfo = { "description": "", "displayName": "CodeBlock", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["components/styleguide/CodeBlock.tsx#CodeBlock"] = { docgenInfo: CodeBlock.__docgenInfo, name: "CodeBlock", path: "components/styleguide/CodeBlock.tsx#CodeBlock" };
}
catch (__react_docgen_typescript_loader_error) { }